.renderWrapper {
  display: flex;
  border: 1px solid lightgray;
  padding: 10px;
  gap: 15px;
  flex-wrap: wrap;
}

.renderWrapperBookInfo {
  flex: 2;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.renderWrapperCurrentState {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

.renderWrapperCurrentChapter {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 10px;
  justify-content: center;
}

.currentChapterTitle {
  margin: 0;
  color: gray;
}

.chapterTitle {
  margin: 0;
}

.btnUpdateProgress {
  font-size: 12px;
  background-color: rgb(94, 94, 230);
  color: white;
  border: none;
  padding: 5px;
  width: 50%;
  font-weight: 700;
  border-radius: 3px;
}

.bookCategory {
  color: gray;
}

.bookTitle {
  margin: 0;
}

.bookAuthor {
  margin: 0 5px 10px 5px;
  color: blue;
}

.buttons {
  display: flex;
}

.buttons > * {
  background-color: white;
  border: none;
  color: blue;
  outline: none;
  border-left: 1px solid rgb(211, 210, 210);
  padding-right: 10px;
  margin-bottom: 10px;
  font-weight: 500;
}

.buttons button:hover {
  cursor: pointer;
}

.buttons button:first-child {
  border: none;
}

.renderWrapperPercentage {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.renderWrapperPercentage h1 {
  margin: 0;
}

.renderWrapperPercentage small {
  color: gray;
  font-weight: 700;
}

.renderWrapperCurrentStateShape {
  display: flex;
  justify-content: center;
  align-items: center;
}
